import React from 'react';
import {IconButton, List} from 'react-native-paper';
import {genExecTime} from '../../api';
import {useAppDispatch, useAppSelector} from '../../store/hooks';
import {
  setActiveLayer,
  setIsPlaying,
} from '../../store/slices/editor';
import {useWindowDimensions} from 'react-native';

export function EditorPlayControls() {
  const {width} = useWindowDimensions();
  const dispatch = useAppDispatch();
  const caretPosition = useAppSelector(
    state => state.editor.caretPosition,
  );
  const scrollPosition = useAppSelector(
    state => state.editor.scrollPosition,
  );
  const pixelsPerSecond = useAppSelector(
    state => state.editor.pixelsPerSecond,
  );
  const isPlaying = useAppSelector(state => state.editor.isPlaying);
  // const isRendering = useAppSelector(
  //   state => state.editor.isRendering,
  // );
  const activeLayer = useAppSelector(
    state => state.editor.activeLayer,
  );
  const currentPosition =
    (caretPosition + scrollPosition) / pixelsPerSecond;
  return (
    <List.Item
      style={{
        width,
        height: 50,
        position: 'absolute',
        bottom: 0,
        left: 0,
        backgroundColor: 'transparent',
        zIndex: 10000,
      }}
      left={p => <List.Icon {...p} icon={'clock'} />}
      title={genExecTime(currentPosition)}
      right={p => (
        <IconButton
          {...p}
          // size={fontSize}
          mode="contained"
          icon={!isPlaying ? 'play' : 'pause'}
          onPress={async () => {
            try {
              // console.info('toggle play', video3.current, video3.current?.playAsync)
              if (isPlaying === false) {
                console.info('start play');
                // dispatch(resetCurrentVideoStateToMain())
                dispatch(
                  setActiveLayer({
                    activeLayerId: activeLayer,
                    caretPosition,
                    scrollPosition,
                  }),
                );
                dispatch(setIsPlaying(true));
                // (video3.current as Video).shouldPlay = true
              } else {
                // await (video3.current as Video).stopAsync()
                dispatch(setIsPlaying(false));
                // dispatch(resetCurrentVideoStateToMain())
              }
            } catch (err) {
              console.error(err);
            }
          }}
        />
      )}
    />
  );
}
