import React from 'react';
import {Divider, IconButton, List} from 'react-native-paper';
import {FolderType, Media} from '../../../types';
import {
  NavigationProp,
  useNavigation,
} from '@react-navigation/native';
import {ProjectsStackedParams} from '../../../types/nav';
function ProjectItem({
  item,
  folderType,
  onRemoveFolder,
}: {
  item: Media;
  folderType: FolderType;
  onRemoveFolder: (item: {id: string}) => void;
}) {
  const navigation =
    useNavigation<NavigationProp<ProjectsStackedParams>>();
  const projectType =
    folderType === FolderType.encoder
      ? 'EncoderProject'
      : 'EditorProject';
  const projectIcon =
    folderType === FolderType.editor ? 'film' : 'matrix';
  return (
    <>
      <Divider />
      <List.Item
        style={{padding: 19}}
        title={item.title}
        onPress={() => {
          navigation.navigate({
            name: projectType,
            params: {
              projectId: item.id,
              mnemonic: item.mnemonic,
              parentFolderId: item.folderId,
            },
          });
        }}
        left={() => <List.Icon icon={projectIcon} />}
        right={() => (
          <IconButton
            icon={'delete'}
            onPress={() => onRemoveFolder(item)}
          />
        )}
      />
    </>
  );
}

export default ProjectItem;
