import React, {useState} from 'react';
import {
  GestureHandlerRootView,
  ScrollView,
} from 'react-native-gesture-handler';
import {
  Portal,
  Modal,
  Card,
  Avatar,
  Button,
  List,
  Divider,
  Banner,
  ProgressBar,
  useTheme,
  Switch,
} from 'react-native-paper';
import {useMutation, useQueryClient} from 'react-query';
import {domain, cdndomain} from '../../../db/defaults';
import {
  EditorLayer,
  EditorProject,
  EncoderProject,
  FFMpegOutputType,
  FFMpegPreset,
  Folder,
  FolderType,
  Media,
  Product,
} from '../../../types';

import {useAppDispatch, useAppSelector} from '../../../store/hooks';
import {
  setEncrypt,
  setFileUploadProgress,
  setMediaModalVisible,
  setUploadInProgress,
  setUploadToCloud,
} from '../../../store/slices/upload';
import {shareFileToWT, writeFileOpfs} from '../../../api/media';
import {useNavigation} from '@react-navigation/native';
import {niceBytes} from '../../../api/util';
import uuid from 'react-native-uuid';
import {getById, getUserData, writeItem} from '../../../api';
import {Platform} from 'react-native';
import {AVPlaybackStatusSuccess, Video} from 'expo-av';
import axios from 'axios';
import {hashData, signData} from '../../../api/ssl';
import {getPremiumKey} from '../../../api/billing';
import {getUserStorageAddress} from '../../../api/exchange';
const preset = FFMpegPreset.superfast;
const output = FFMpegOutputType.mp4;
const compressionLevel = 20;

export default function FileUpload(props: any) {
  //FILE UPLOAD
  const {
    userId,
    folderType,
    hideMedialModal,
    folderId,
    routeName,
    projectId,
    isEncoderProject,
  } = props;
  const theme = useTheme();
  const queryClient = useQueryClient();
  const uploadState = useAppSelector(state => state.upload);
  const dispatch = useAppDispatch();
  const nav = useNavigation();
  const mediaModalVisible = uploadState.modalVisible;
  const pickerFile = uploadState.file;
  const pickerFileMeta = uploadState.media;
  const uploadToCloud = uploadState.uploadToCloud;
  const shareToWt = uploadState.shareToWT;
  const encryptionEnabled = uploadState.encryption;
  const fileUploadProgress = uploadState.fileUploadProgress;
  const isUploadInProgress = uploadState.isUploadInProgress;
  const uuidFilename = `${pickerFileMeta?.id}-${pickerFileMeta?.filename}`;
  const writeItemMutation = useMutation(
    (item: Media | Folder | EditorProject | EditorLayer) =>
      writeItem(item),
  );
  const [videoMeta, setVideoMeta] = useState({});
  const uploadFileMutation = useMutation(async () => {
    const userContractAddress = await getUserStorageAddress();
    const key = await getPremiumKey();
    const signature = await signData(
      key.keyPair.privateKey as JsonWebKey,
      userContractAddress,
    );
    const res = await axios.request({
      url: `${domain}api/v1/backend/files/${uuidFilename}?userContractAddress=${userContractAddress}&signature=${signature}`,
      method: 'PUT',
      data: pickerFile,
      onUploadProgress: progress =>
        dispatch(setFileUploadProgress(progress)),
    });
    return res.data as {result: string};
  });

  const writeItemToLocalStoreMutation = useMutation(meta =>
    writeFileOpfs({...meta, ...videoMeta}, pickerFile!),
  );

  const writingItem =
    writeItemMutation.isLoading || uploadFileMutation.isLoading;
  let peerShared = shareToWt;
  if (pickerFileMeta?.extractor === 'wt') {
    peerShared = true;
  }
  const confirmFile = async () => {
    try {
      const meta: Media = JSON.parse(JSON.stringify(pickerFileMeta!));
      const {width, height} = videoMeta;
      const end = videoMeta.duration - 1;
      console.info('routeName', routeName, 'folderType', folderType);
      meta.isDownloaded = true;
      let folder: Folder | undefined;
      try {
        folder = await getById(meta.folderId);
        if (!folder) {
          folder = (await getById(meta.folderId, folderId)) as Folder;
        }
        console.info(
          'project folder',
          folder,
          folderId,
          meta.folderId,
        );
        if (folder?.mnemonic) {
          peerShared = true;
        }
      } catch (err) {
        console.error('err', err);
      }
      if (
        folderType === FolderType.editor &&
        routeName !== 'EditorProject'
      ) {
        console.info('creating a new editor project from file');
        const editor: EditorProject = {
          id: uuid.v4().toString(),
          title: meta.title,
          userId,
          isEditorProject: true,
          folderType,
          folderId,
          orderId: 1,
          isFolder: false,
          editor: {
            length: 1 * 60 * 1000,
            width,
            height,
            preset,
            output,
            lastUpdated: new Date().valueOf(),
            compressionLevel,
            code: [],
          },
          mnemonic: folder?.mnemonic,
          date: new Date().valueOf(),
        };
        await writeItemMutation.mutateAsync(editor);
        const layer: EditorLayer = {
          id: uuid.v4().toString(),
          title: 'Layer 1',
          folderId: editor.id,
          userId,
          isEditorLayer: true,
          orderId: 1,
          folderType,
          isFolder: false,
          date: new Date().valueOf(),
          editor: {
            videoDisabled: false,
            muted: false,
          },
          mnemonic: folder?.mnemonic,
        };
        await writeItemMutation.mutateAsync(layer);
        const comment_layer: EditorLayer = {
          id: editor.id + '_comments',
          title: 'Comments',
          folderId: editor.id,
          userId,
          isEditorLayer: true,
          orderId: 1,
          folderType,
          isFolder: false,
          date: new Date().valueOf(),
          editor: {
            videoDisabled: true,
            muted: true,
            isCommentLayer: true,
          },
          mnemonic: folder?.mnemonic,
        };
        await writeItemMutation.mutateAsync(comment_layer);
        meta.folderId = layer.id;
        meta.isMediaNew = isEncoderProject;
        meta.isEncoderProject = isEncoderProject;
        meta.encoding = {
          end,
          width,
          height,
          preset,
          output,
          compressionLevel,
        };
      }
      if (folderType === FolderType.editor) {
        (meta as EncoderProject).encoding = {
          end,
          width,
          height,
          preset,
          output,
          compressionLevel,
        };
      }
      if (folderType === FolderType.shop) {
        (meta as Product).isProduct = true;
        (meta as Product).price = 1;
      }
      console.info('foldertype saveitem', folderType, meta);
      await writeItemToLocalStoreMutation.mutateAsync(meta);
      await writeItemMutation.mutateAsync({...meta, ...videoMeta});
      try {
        if (peerShared) shareFileToWT(meta.id, meta.folderId);
      } catch (err) {
        console.error('share to wt error', err);
      }
      if (uploadToCloud) {
        dispatch(setUploadInProgress(true));
        dispatch(setMediaModalVisible(false));
        const uploadRes = await uploadFileMutation.mutateAsync();
        const hashId = (await hashData(undefined, userId)).split('/');
        const hash =
          encodeURIComponent(hashId[0]) +
          '/' +
          encodeURIComponent(hashId[1]);
        const fileUrl = cdndomain + uploadRes.result;
        meta.webpage_url = fileUrl;
        meta.hasCloud = true;
        meta.extractor = 'cloud';
        meta.url = fileUrl;
        console.info('after upload File Mutation', meta);
        await writeItemMutation.mutateAsync({...meta, ...videoMeta});
      }
      await queryClient.refetchQueries({
        queryKey: ['folderContent_' + meta.folderId],
      });
      await queryClient.refetchQueries({
        queryKey: ['folderContent_' + folderId],
      });
      await queryClient.refetchQueries({
        queryKey: ['projectLayers', projectId],
      });
      await queryClient.refetchQueries({
        queryKey: ['encodedMedia', projectId],
      });
      dispatch(setUploadInProgress(false));
      dispatch(setMediaModalVisible(false));
      if (!folderId) {
        nav.navigate('Folder', {folderId: meta.folderId});
      }
    } catch (err) {
      console.error('media upload modal error', err);
    }
  };
  return (
    <>
      <Banner theme={theme} visible={isUploadInProgress}>
        <Card.Title
          titleStyle={{fontSize: 20}}
          title="Uploading file"
          subtitle={pickerFileMeta?.filename}
          left={props => <Avatar.Icon {...props} icon="file" />}
        />
        <Divider />
        <ProgressBar
          animatedValue={fileUploadProgress?.progress || 0}
        />
      </Banner>
      <Portal>
        <Modal
          visible={mediaModalVisible}
          onDismiss={hideMedialModal}
          theme={theme}
          style={{padding: 20}}>
          <Card>
            <Card.Title
              titleStyle={{fontSize: 20}}
              title="Adding a new file"
              subtitle="Please confirm"
              left={props => <Avatar.Icon {...props} icon="file" />}
            />
            <Divider />
            <GestureHandlerRootView style={{height: '70%'}}>
              <ScrollView>
                <Card.Content>
                  <List.Section>
                    <List.Item
                      title={'Filename'}
                      description={pickerFileMeta?.filename}
                    />
                    <List.Item
                      title={'Format'}
                      description={pickerFileMeta?.type}
                    />
                    <List.Item
                      title={'Duration'}
                      description={videoMeta?.duration}
                    />
                    <List.Item
                      title={'Width'}
                      description={videoMeta?.width}
                    />
                    <List.Item
                      title={'Height'}
                      description={videoMeta?.height}
                    />
                    <List.Item
                      title={'Size'}
                      description={niceBytes(
                        pickerFileMeta?.size?.toString(),
                      )}
                    />
                    <List.Item
                      title={'FolderId'}
                      description={pickerFileMeta?.folderId}
                    />
                    {pickerFile && Platform.OS === 'web' ? (
                      <video
                        style={{display: 'none'}}
                        src={URL.createObjectURL(pickerFile)}
                        onLoadedMetadata={e => {
                          // console.info("metaLoaded", e.nativeEvent.target, e.nativeEvent.target.videoWidth, e.nativeEvent.target.videoHeight, e.nativeEvent.target.duration)
                          // window.video1 = e.nativeEvent.target
                          setVideoMeta({
                            width: e.nativeEvent.target.videoWidth,
                            height: e.nativeEvent.target.videoHeight,
                            duration: e.nativeEvent.target.duration,
                          });
                        }}
                      />
                    ) : (
                      <Video
                        // style={{display: 'none'}}
                        source={{uri: pickerFileMeta?.url as string}}
                        onLoad={e => {
                          e;
                        }}
                        onReadyForDisplay={e => {
                          console.info('metaLoaded', e);
                          // window.video1 = e.nativeEvent.target
                          setVideoMeta({
                            width: e.naturalSize.width,
                            height: e.naturalSize.height,
                            duration:
                              ((e.status as AVPlaybackStatusSuccess)
                                ?.durationMillis || 0) / 1000,
                          });
                        }}
                      />
                    )}
                    {/* <List.Item 
                  onPress={()=>dispatch(setShareToWt(!shareToWt))}
                  title={"Share To WebTorrent"} 
                  right={(props)=><Switch theme={theme} value={peerShared || shareToWt}/>
                } /> */}
                    <List.Item
                      onPress={() =>
                        dispatch(setUploadToCloud(!uploadToCloud))
                      }
                      title={'Upload To Cloud'}
                      right={props => (
                        <Switch theme={theme} value={uploadToCloud} />
                      )}
                    />
                    <List.Item
                      onPress={() =>
                        dispatch(setEncrypt(!encryptionEnabled))
                      }
                      title={'Encrypt'}
                      right={props => (
                        <Switch
                          theme={theme}
                          value={encryptionEnabled}
                        />
                      )}
                    />
                  </List.Section>
                </Card.Content>
              </ScrollView>
            </GestureHandlerRootView>
            <Divider />
            <Card.Actions style={{height: 'auto'}}>
              <Button
                disabled={writingItem}
                onPress={hideMedialModal}>
                Cancel
              </Button>
              <Button
                loading={writeItemMutation.isLoading}
                onPress={confirmFile}>
                Confirm
              </Button>
            </Card.Actions>
          </Card>
        </Modal>
      </Portal>
    </>
  );
}
